"use client";

import React, { useEffect, useState } from 'react';
import { AdminLayout } from '@/components/ui/admin-layout';
import { useRouter, usePathname } from 'next/navigation';
import { useAuth } from '@/context/auth-context';

export default function Layout({
  children,
}: {
  children: React.ReactNode;
}) {
  const { isAuthenticated, isInitialized } = useAuth();
  const router = useRouter();
  const pathname = usePathname();
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    if (!isInitialized) return;

    const timer = setTimeout(() => {
      // If we're not on the login page and not authenticated, redirect to login
      if (!isAuthenticated && pathname !== '/admin/login') {
        // Use window.location for a full page navigation to avoid React state issues
        window.location.href = '/admin/login';
        return;
      }

      // If we're on the login page and already authenticated, redirect to admin home
      if (isAuthenticated && pathname === '/admin/login') {
        window.location.href = '/admin';
        return;
      }

      setIsLoading(false);
    }, 200); // Small delay to ensure state is stable

    return () => clearTimeout(timer);
  }, [isAuthenticated, isInitialized, pathname]);

  // Show nothing during authentication check
  if (isLoading || (!isAuthenticated && pathname !== '/admin/login')) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-100 dark:bg-gray-900">
        <div className="bg-white dark:bg-gray-800 p-8 rounded-lg shadow-lg text-center">
          <div className="w-16 h-16 border-4 border-blue-500 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-700 dark:text-gray-300">로딩 중...</p>
        </div>
      </div>
    );
  }

  return <AdminLayout>{children}</AdminLayout>;
}
